.complete {
   padding: 5.25rem 1.5rem 4.75rem;
   display: flex;
   flex-direction: column;
   align-items: center;
}

.complete__icon {
   background: url(../../images/icon-complete.svg) center / contain no-repeat;
   display: inline-block;
   width: 80px;
   height: 80px;
   margin-bottom: 2.5rem;
}

.complete__title {
   font-size: 1.75rem;
   font-weight: 600;
   letter-spacing: 3px;
   margin-bottom: 1.5rem;
}

.complete__subtitle {
   font-weight: 500;
   font-size: 1.1rem;
   letter-spacing: 0px;
   color: var(--dark-grey);
   margin-bottom: 3.25rem;
}

@media screen and (min-width: 1440px) {
   .complete {
      margin: auto 13rem auto auto;
      width: 380px;
      height: min-content;
   }
}